@import url(https://fonts.googleapis.com/css2?family=EB+Garamond:wght@800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  overflow: hidden;
}
.heading {
  margin-top: 3.5em;
  text-align: center;
}
.heading-wrapper {
  position: relative;
  display: inline-block;
}
.btn-wrapper {
  margin: 20px;
  height: 50px;
  width: 11em;
  border-radius: 25px;
}

.btn-wrapper:hover {
  cursor: pointer;
}
/* Style for Headings and Information associated with each heading*/
.heading-btn {
  font-size: 1.5em;
  height: 120%; /* needed to make anchor element fit div wrapper*/
  border-radius: 25px !important;
  border: 2px solid;
}

/*progress section*/
.progress.row {
  margin-top: 2em;
  text-align: center;
}
.proficiency {
  position: relative;
  display: inline-block;
}
/*animation*/
.progress-bar {
  width: 0;
  -webkit-animation: progress 1.5s ease-in-out forwards;
          animation: progress 1.5s ease-in-out forwards;
}
.title {
  opacity: 0;
  -webkit-animation: show 0.35s forwards ease-in-out 0.5s;
          animation: show 0.35s forwards ease-in-out 0.5s;
}

@-webkit-keyframes progress {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes progress {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@-webkit-keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/*end of progress section*/
.card {
