/*Adjustments to side panel buttons*/
.side-button {
  display: block;
  width: 200px;
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  font-family: sans-serif;
  text-decoration: none;
  color: #333;
  border: 2px solid #333;
  letter-spacing: 2px;
  text-align: center;
  position: relative;
  transition: all 0.35s;
  background-color: white;
  margin-left: 2em;
  border-radius: 25px;
}

.side-button span {
  position: relative;
  z-index: 2;
}
/*animation*/

.side-button:after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: #007bff;
  transition: all 0.35s;
  border-radius: 25px;
}

.side-button:hover {
  color: #fff;
}

.side-button:hover:after {
  width: 100%;
}

/*end of side panel buttons*/

