body {
  overflow: hidden;
}
.heading {
  margin-top: 3.5em;
  text-align: center;
}
.heading-wrapper {
  position: relative;
  display: inline-block;
}
.btn-wrapper {
  margin: 20px;
  height: 50px;
  width: 11em;
  border-radius: 25px;
}

.btn-wrapper:hover {
  cursor: pointer;
}
/* Style for Headings and Information associated with each heading*/
.heading-btn {
  font-size: 1.5em;
  height: 120%; /* needed to make anchor element fit div wrapper*/
  border-radius: 25px !important;
  border: 2px solid;
}

/*progress section*/
.progress.row {
  margin-top: 2em;
  text-align: center;
}
.proficiency {
  position: relative;
  display: inline-block;
}
/*animation*/
.progress-bar {
  width: 0;
  animation: progress 1.5s ease-in-out forwards;
}
.title {
  opacity: 0;
  animation: show 0.35s forwards ease-in-out 0.5s;
}

@keyframes progress {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/*end of progress section*/
.card {
  margin-top: 1.1em;
  border: none;
}
/*general card adjustments for collapse body*/
.collapse.show {
  width: 60em;
}
.collapsing {
  width: 60em;
}
/*end of general card adjustments for collapse body*/

/*general side panel adjustments*/
.side-panel {
  background-color: #f5f5f5;
